*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'titllium web', sans-serif;
}
:root{
  --maincolor:#29335c;
  --mainColorLight:#5767aa;
  --secondaryColor: #DB2B39;
  --textColor:#eee;

}
.header{
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 80px;
  padding: 0 2rem;
  background-color: var(--maincolor);
  color: var(--textColor);
  /* position: fixed; */
}

nav a{
  margin: 0 2rem;
  color: var(--textColor);
  text-decoration: none;


}
nav a:hover{
  color: var(--secondaryColor);
}
 .nav-btn{
  padding: 5px;
  cursor: pointer;
  background: transparent;
  border: none;
  outline: none;
  color: var(--textColor);
   visibility: hidden; 
  opacity: 0;
  font-size: 1.8rem;
 }
  @media only screen and ( max-width: 1024px){
   .nav-btn{
      visibility: visible;
      opacity: 1;
    }
    .header nav{
      position: fixed;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      gap: 1.5rem;
      background-color: var(--maincolor) ;
      transition: 1s;
     transform: translateY(-100vh); 
    }
    .header .responsive_nav{
      transform: none;
    }
    .header{
      visibility: visible;
      display: flex;
    }
  nav .nav-close-btn{
    position: absolute;
    top: 1.2rem;
    right: 1.3rem;
  }
  .header .logo{
    display: flex;
  }
  }
 
